<template>
  <el-dialog id="FengxianpingguSee"  :visible.sync="local.FengxianpingguSee_show" width="600px" :close-on-click-modal="false">
    <div slot="title" class="dialog-title">新增风险评估</div>
    <!-- :rules="rules"   验证 -->
    <el-form ref="ruleForm" :rules="rules" :model="ruleForm" label-width="120px" class="demo-ruleForm">
      <el-form-item label="风险评估类型：">
        <el-select v-model="ruleForm.selectval" placeholder="请选择" @change="checktime">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="风险评估时间：">
        <el-date-picker :editable="false"  v-show="checkweek"  v-model="value1" type="week"  placeholder="选择周" format="yyyy第WW周" value-format="yyyy-MM-dd"  @change="checkweekval"></el-date-picker>
        <el-date-picker v-show="checkmonth"  v-model="value2" type="month" placeholder="选择月" format="yyyy年MM月" value-format="yyyy年MM月" @change="checkmonthval"></el-date-picker>
        <el-date-picker v-show="checkyear"  v-model="value3" type="year" placeholder="选择年" format="yyyy年" value-format="yyyy年" @change="checkyearval"></el-date-picker>
      </el-form-item>
      <el-form-item label="风险评估名称：" prop="bbb">
        <el-input v-model="ruleForm.bbb"  width="100px"></el-input>
      </el-form-item>
      <el-form-item label="基态BPA文件：">
        <el-input v-model="JT_BPA_file.name"></el-input>
        <img src="../../static/img/文件夹.png" id="BPA_file">
        <input id="BPA_files" type="file" @change="Upload1">
      </el-form-item>
      <el-form-item label="检修BPA文件：">
        <div class="Overhaul">
            <div class="BPA_num">序号</div>
            <div class="BPA_name">文件名</div>
          </div>
        <div style="width:420px;max-height: 130px;overflow: auto;">
          <div class="Overhaul_comment" v-for="(value,num) in fileList">
            <div class="BPA_num1">{{num+1}}</div>
            <div class="BPA_name1">{{value.name}}</div>
          </div>
        </div>
        
        <!-- <el-input v-model="ruleForm.name"></el-input> -->
      </el-form-item>
        <img class="add_BPA" src="../../static/img/添加.png">
        <input id="myfile" type="file" multiple @change="Upload">
      <!-- </label> -->
      <img class="DeleteAll" src="../../static/img/减号.png" title="删除全部" @click="DeleteAll">
      <el-form-item label="检修E文件：" prop="name">
        <el-input v-model="E_file.name"></el-input>
        <img src="../../static/img/文件夹.png" id="E_file">
        <input id="E_files" type="file"  @change="Upload2">
      </el-form-item>
      <div class="add_btn">
        <el-button type="primary" size="mini" @click="submitForm('ruleForm')">保存</el-button>
        <el-button type="primary" size="mini" @click="StartCal">开始计算</el-button>
        <el-button type="primary" size="mini" @click="resetForm('ruleForm')">取消</el-button>
      </div>
  </el-form>
  </el-dialog>
</template>

<script>
import DataLocal from './DateLocal.js'
import { getWeek } from './public.js';
export default {
    data(){
       return {
         local:DataLocal,

         options: [{
          value: '年评估',
          label: '年评估'
          }, {
            value: '月评估',
            label: '月评估'
          }, {
            value: '周评估',
            label: '周评估'
          }],
        selVal: '',//风险评估类型下拉选中的值

         value1: '',  //周
         value2: '',  //月
         value3: '',  //年
         checkweek:false,
         checkmonth:true,
         checkyear:false,
         

         ruleForm: {
          selectval: '月评估',  // 选取默认值
          region: '',
          datetime: '',
          name:'',
          bbb:''
        },
        // 校验规则
        rules: {
          bbb: [
                {required: true, message: '必填', trigger: 'blur'}
              ]
        },

        JT_BPA_file:'',  //基态BPA文件
        E_file:'',     //检修E文件
        fileList:[]  //上传文件数据

       }
    },
    created(){
       
    },
    mounted(){
        
    },
    watch: {
      filterText(val) {
        this.$refs.tree.filter(val);
      }
    },
    methods:{
      hidden(){
          this.local.FengxianpingguSee_show = false
        },
      submitForm(ruleForm) { //保存
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            alert('提交');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(ruleForm) {  //取消
        this.$refs[ruleForm].resetFields();
      },
      StartCal(){  //开始计算
        this.$confirm('是否进行XXXX计算?', '风险评估计算确认', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'info',
                    message: '已开始计算',
                    duration:'1500'
                });
                this.local.FengxianpingguSee_show = false
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消计算',
                    duration:'1500'
                });          
            });
      },
      checktime(value){  //风险评估类型下拉
        console.log(value)
        // if(value = "year") return this.checkyear = true
        this.selVal = value
        if(value == "年评估"){
          this.checkyear = true
          this.checkmonth = false
          this.checkweek = false
        }else if(value == "周评估"){
          this.checkyear = false
          this.checkmonth = false
          this.checkweek = true
        }
        if(value == "月评估"){
          this.checkyear = false
          this.checkmonth = true
          this.checkweek = false
        }
      },
      Upload(){ //检修BPA文件上传
        let b = myfile.files
        // console.log(b)
        // console.log(myfile.files)
        for (var i = 0; i < b.length; i++) {
          // console.log(b[i])
          this.fileList.push(b[i])
        }
        console.log(this.fileList)
      },
      Upload1(){  //基态BPA文件上传
        // let b = BPA_files.files
        this.JT_BPA_file = BPA_files.files[0]
        // console.log(this.JT_BPA_file.name)
      },
      Upload2(){ //检修E文件上传
        this.E_file = E_files.files[0]
      },
      DeleteAll(){  //检修BPA文件删除
        let lg= this.fileList.length
        if(lg>0){
            this.$confirm('确定删除全部文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.fileList = []
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          })
        }else{
           this.$message({
              type: 'info ',
              message: '暂无文件可以删除!',
              duration:'1500'
              // center: true
            });
        }
        console.log(lg)
        // <el-progress :percentage="50"></el-progress>
      },
      
      checkweekval(value1){
        var datatime=new Date(value1);
        var Y = datatime.getFullYear()
        console.log(Y)
        console.log(getWeek( new Date(value1))+1)
        let weekvalue = getWeek( new Date(value1))+1
        this.ruleForm.bbb = this.ruleForm.selectval+'-'+Y+'年'+'第'+weekvalue+'周'
      },
      checkmonthval(value2){
        console.log(value2)
        // console.log(this.ruleForm.selectval)
        this.ruleForm.bbb = this.ruleForm.selectval+'-'+value2
        // alert(1)
      },
      checkyearval(value3){
        console.log(value3)
        // console.log(this.ruleForm.selectval)
        this.ruleForm.bbb = this.ruleForm.selectval+'-'+value3
        // alert(1)
      }
    
       
      
    },
    destroyed:function(){
      // this.local.desc = ''
    }
}
</script>

<style>
/* .el-date-editor,.el-input{
    width:91%;
} */
.el-dialog__headerbtn:focus .el-dialog__close, .el-dialog__headerbtn:hover .el-dialog__close {
    color: #ffffff;
}
.el-input{
  width: 91% !important;
}
.el-dialog__header {
  padding: 0;
  /* width:600px; */
  height:30px;
  line-height: 30px;
  background:rgba(51,158,211,1);
  border-radius:5px 5px 0px 0px;

}
#FengxianpingguSee .el-dialog__body{
  padding: 10px 10px;
}
.el-dialog__footer{
  padding: 0 0 10px 0;
  text-align: left;
}
.dialog-title{
  width: 80px;
  font-size:12px;
  font-family:Microsoft YaHei;
  font-weight:bold;
  color:rgba(255,255,255,1);
  margin-left: 10px
}
.el-dialog__headerbtn{
  top: 8px;
}
.el-dialog__headerbtn .el-dialog__close {
    color: #fff;
}

.el-form-item__label{
  font-size:12px;
  font-family:Microsoft YaHei;
  font-weight:400;
  color:rgba(51,158,211,1);
}
.el-input__inner{
  height: 30px;
}
.el-form-item{
  margin-bottom: 10px
}
.el-form-item__error{
  top: 88%
}
.Overhaul{
  width: 420px;
  /* background:rgba(255,255,255,1); */
  /* border:1px solid rgba(204,204,204,1); */
  /* border-radius:2px;
  min-height: 20px; */
  margin-top: 8px;
}
.Overhaul div{
  height:25px;
  line-height: 25px;
  float: left;
  font-size:12px;
  font-family:Microsoft YaHei;
  font-weight:400;
  color:rgba(77,77,77,1);
  text-align: center;
  background:linear-gradient(0deg,rgba(230,230,230,0.8),rgba(255,255,255,0.8));
  /* border-right: 1px solid rgba(204,204,204,1);
  border-bottom: 1px solid rgba(204,204,204,1); */
}
.BPA_num,.BPA_num1{
  width: 20%;
  
}
.BPA_name,.BPA_name1{
  width: 79%;
}
.BPA_num{
   border: 1px solid #DCDFE6;
   border-right: none
   /* border-bottom: 1px solid rgba(204,204,204,1); */
}
.BPA_name{
   border: 1px solid #DCDFE6;
}
.BPA_num1{
   border-left: 1px solid #DCDFE6;
   border-bottom: 1px solid #DCDFE6;
}
.BPA_name1{
   border-left: 1px solid #DCDFE6;
   border-right: 1px solid #DCDFE6;
   border-bottom: 1px solid #DCDFE6;
}
.Overhaul_comment{
   width: 420px;
  background:rgba(255,255,255,1);
  border-radius:2px;
  max-height: 135px;
}
.Overhaul_comment div{
  float: left;
  height:25px;
  line-height: 25px;
  font-size:12px;
  font-family:Microsoft YaHei;
  font-weight:400;
  color:rgba(77,77,77,1);
  text-align: center;
}
.add_btn{
  text-align: center;
  margin-top: 10px
}
.add_btn button{
  height:24px;
  background:rgba(51,158,211,1);
  box-shadow:1px 1px 1px 0px rgba(46,50,52,0.3);
  border-radius:2px;
  border: none;
  color: #fff
}


/* .fileinput-button {
  display: inline-block;
  overflow: hidden;
  position: absolute;
  right: 25px;
  top: 209px;
} */
.add_BPA{
  position: absolute;
  right: 29px;
  top: 250px;
  cursor: pointer;
  /* z-index: 9999; */
}
#myfile{
 position: absolute;
  right: 29px;
  top: 245px;
  opacity: 0;
  width: 16px;
}
.DeleteAll{
  position: absolute;
  right: 8px;
  top: 250px;
  cursor: pointer;

}

#BPA_file{
    position: absolute;
    right: 45px;
    top: 14px;
}
#BPA_files{
  position: absolute;
  right: 45px;
    top: 14px;
  opacity: 0;
  width: 16px;
}
#E_file{
  position: absolute;
    right: 45px;
    top: 14px;
}
#E_files{
  position: absolute;
  right: 45px;
    top: 14px;
  opacity: 0;
  width: 16px;
}
</style>



