import Vue from 'vue'
import Router from 'vue-router'
const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error=> error)
}
import Fengxianpinggu from '@/components/Fengxianpinggu'
import Fengxianpinggu_monthDetail from '@/components/Fengxianpinggu_monthDetail'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {                //风险评估主页头部
      path: '/',
      name: 'Fengxianpinggu',
      component: Fengxianpinggu
    },
    {              //跳转风险评估列表结果
      path: '/Fengxianpinggu_monthDetail',
      name: 'Fengxianpinggu_monthDetail',
      component: Fengxianpinggu_monthDetail,
    },
    
  ]
})



