<template>
  <div id="TableDetail">
    <div id="Search">
        <img src="../../static/img/search.png">
        <input class="search" type="text" v-model="search" placeholder="搜索">
    </div>
    <el-table border  :data="tables" style="width: 100%" @row-click="RowClick" max-height="520px" 
    :header-cell-style="tableHeaderColor" :row-class-name="tableRowClassName">
    <el-table-column type="index" width="50" label="序号">
    </el-table-column>
        <el-table-column prop="people" label="评估名称" width="150" align="center"></el-table-column>
        <el-table-column prop="jindu"  label="状态" width="150" align="center">
            <template slot-scope="scope">
                <div class="Un_calculation" v-show="scope.row.jindu ==1">未计算</div>
                <div class="In_calculation" v-show="scope.row.jindu ==0">计算中<i class="el-icon-loading"></i></div>
                <div class="calculation_success" v-show="scope.row.jindu ==3">计算成功</div>
                <div class="calculation_fail" v-show="scope.row.jindu ==4">计算失败</div>
                <div v-show="scope.row.jindu ==5">已生成预警单</div>
             </template>
        </el-table-column>
        <el-table-column prop="meet"  label="评估类别" width="150" align="center"></el-table-column>
        <el-table-column prop="place" label="检修计划" width="200" align="center">
             <template slot-scope="scope">
                <div class="MaintenancePlans_div" v-html="scope.row.place" @click="MaintenancePlans">        
                </div>
             </template>
        </el-table-column>
        <el-table-column prop="execg" label="评估时间" width="200" align="center"></el-table-column>
        <el-table-column prop="works" label="评估人员" width="200" align="center"></el-table-column>
        <el-table-column label="操作"  align="center">
            <template slot-scope="scope">
                <button @click="jump(scope.$index,scope.row)"><img src="../../static/img/结果.png"> 结果</button>
                <button @click="TableDetail_del(scope.$index,scope.row)"><img src="../../static/img/删除.png"> 删除</button>
            </template>
            <!-- <button><img src="../../static/img/修改.png" alt=""> 修改</button>
            <button><img src="../../static/img/云计算.png" alt=""> 计算</button> -->
            <!-- <button><img src="../../static/img/删除.png" alt=""> 删除</button> -->
            <!-- <button @click="Table_see"><img src="../../static/img/查看.png"> 查看</button> -->
            <!-- <button><img src="../../static/img/结果.png" alt=""> 结果</button> -->
        </el-table-column>
    </el-table>
   
    <!-- 弹窗--新增风险评估 -->
    <FengxianpingguSee v-if="local.FengxianpingguSee_show"></FengxianpingguSee>
    <!-- 弹窗--已生成预警单 -->
    <YishengchengYujingdan v-if="local.YishengchengYujingdan"></YishengchengYujingdan>

     <!-- 弹窗--已生成预警单 -->
    <!-- <FengxianpingguMonthDetail v-if="local.Fengxianpinggu_monthDetail_show"></FengxianpingguMonthDetail> -->
    
    <!--弹窗  检修计划 -->
    <MaintenancePlan v-if="local.MaintenancePlan_show"></MaintenancePlan>
  </div>
  
</template>
<script>
import DataLocal from './DateLocal.js'
// import Yujingtongzhidan from './Yujingtongzhidan'
import FengxianpingguSee from './FengxianpingguSee'
import YishengchengYujingdan from './YishengchengYujingdan'
// import Fengxianpinggu_monthDetail from './Fengxianpinggu_monthDetail'
import MaintenancePlan from './MaintenancePlan'
    export default {
        data () {
            return {
                local:DataLocal,

                dormitory: [{
                people: '年风险评估-2020年',
                jindu:'1',
                meet: '年评估',
                place: '北京检修计划名称',
                execg: '2012',
                works: '小张'
                }, {
                people: '年风险评估-2020年',
                jindu:'0',
                meet: '年评估',
                place: '上海检修计划名称',
                execg: '2019',
                works: '小夏'
                }],
                search: '',

            }
        },
        computed: {
            // 模糊搜索
            tables () {
                const search = this.search
                if (search) {
                return this.dormitory.filter(data => {
                    return Object.keys(data).some(key => {
                        return String(data[key]).toLowerCase().indexOf(search) > -1
                    })
                })
                }
                return this.dormitory
            }
        },
        methods:{
            // filterTag(value, row) {
            //     return row.people === value;
            // },
            jump(index, rows){
                console.log(rows.people)
                const { href } = this.$router.resolve({
                    path: '/Fengxianpinggu_monthDetail',
                    query: { id: 2 }
                })
                window.open(href, '_blank',)
            },
            	
			// formatjindu: function (row, column) {//进度显示转换
			// 	return row.jindu == 1 ? '未计算' : row.jindu == 0 ? '已计算' : '未知';
            // },
            RowClick(data){
                console.log(data)
                // this.local.Yujingtongzhidan_show = true;
            },
            tableRowClassName({row, rowIndex}) {
                if(rowIndex%2==1){
                    return 'warning-row';
                }else{
                    return 'success-row';
                }
            },
            tableHeaderColor({ row, column, rowIndex, columnIndex }) {
                if (rowIndex === 0) {
                    return 'background:linear-gradient(0deg,rgba(230,230,230,0.8),rgba(255,255,255,0.8));color:rgba(77,77,77,1);;font-weight:400;font-size:12px;text-align:center'
                }
            },
            TableDetail_del(index, rows){
            console.log(index)
            console.log(rows.people)
            this.$confirm('是否删除?', '删除', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除',
                    duration:'1500'
                });          
            });
            // rows.splice(index, 1);
          },
          MaintenancePlans(){
            //   alert("查看")
            //   this.local.FengxianpingguSee_show = true
              this.local.MaintenancePlan_show = true
          }
        },
        components: {
            // Yujingtongzhidan,
            FengxianpingguSee,
            YishengchengYujingdan,
            // Fengxianpinggu_monthDetail,
            MaintenancePlan
        },
    }
</script>

<style>
 .el-table .warning-row {
    background:rgba(231,247,255,1);
  }
  .el-table .success-row {
    background:rgba(255,255,255,1);
  }

#Search{
    width: 100%;
    text-align: center;
    line-height: 35px;
    height:40px;
    background:linear-gradient(0deg,rgba(215,238,252,0.8),rgba(255,255,255,0.8));
    position: relative;
    /* border:2px solid rgba(204,220,229,1); */
}
.search{
    width: 95%;
    /* width:1346px; */
    height:22px;
    background:rgba(255,255,255,1);
    border:2px solid rgba(204,204,204,1);
    box-shadow:0px 3px 3px 0px rgba(153,153,153,0.6);
    border-radius:2px;
    padding-left: 2%;
    
}
#Search img{
    position: absolute;
    left: 2%;
    top: 11px;
}
#TableDetail .el-table__header tr,#TableDetail .el-table__header th {
    padding: 0px;
}
.cell button{
    width:57px;
    height:24px;
    background:rgba(51,158,211,1);
    box-shadow:1px 1px 1px 0px rgba(46,50,52,0.3);
    border-radius:2px;
    border: none;
    color: #fff;
    cursor: pointer;
}
.link-btn{
    color: #fff;
    text-decoration: none
}
.MaintenancePlans_div{
    text-decoration: underline;
    color:rgba(51,158,211,1); 
    cursor: pointer;
}
.Un_calculation{
    color: #FF0000
}
.In_calculation{

}
.calculation_success{
    color: #3FB80A
}
.calculation_fail{
    color: #FF0000
}
</style>


