<template>
  <div id="Fengxianpinggu_Detail">
      <div class="Fengxianpinggu_Detail">
        <img src="../../static/img/风险评估标题.png">
        <div>风险评估-月风险评估详情</div>
      </div>

      <ul class="Fengxianpinggu_Detail_Button">
        <li>全部风险记录</li>
        <li>第19周</li>
        <li>第20周</li>
      </ul>

      <div class="Fengxianpinggu_monthDetail_search">
        <img src="../../static/img/search.png">
        <input type="text" v-model="search" placeholder="搜索" style="margin-right:20px">
         <el-checkbox v-model="checked" @change="check">隐藏已生成的预警单记录</el-checkbox>
        <button class="Yujingdan" @click="Shengchengyujingdan">生成预警单</button>
      </div>
      <el-table border :data="tables" style="width: 100%" max-height="520px" 
      :header-cell-style="tableHeaderColor" :row-class-name="tableRowClassName">
        <el-table-column width="65" align="center">
          <template slot-scope="scope">
            <el-radio :label="scope.$index" v-model="radio" @change.native="getCurrentRow(scope.row)"></el-radio>
          </template>
        </el-table-column>
        <el-table-column type="index" width="80" label="序号" align="center"></el-table-column>
        <el-table-column prop="people" width="100" label="检修组id"  align="center"></el-table-column>
        <el-table-column prop="jindu"  label="检修组名称" width="100" align="center"></el-table-column>
        <el-table-column prop="meet"  label="检修设备类型" width="100" align="center"></el-table-column>
        <el-table-column prop="place"   label="检修设备" width="100" align="center">
          <template slot-scope="scope">
            <div class="aaaa">
              {{scope.row.place}}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="execg" label="停电时间" width="100" align="center"></el-table-column>
        <el-table-column prop="works" label="工作内容" width="100" align="center"></el-table-column>
        <el-table-column prop="works" label="风险分析" width="100" align="center"></el-table-column>
        <el-table-column prop="works" label="运行方式调整" width="100" align="center"></el-table-column>
        <el-table-column prop="works" label="控制限额" width="100" align="center"></el-table-column>
        <el-table-column prop="works" label="预控措施" width="100" align="center"></el-table-column>
        <el-table-column label="预想故障" width="600" align="center">
          <el-table-column prop="works" label="序号" width="80" align="center"></el-table-column>
          <el-table-column prop="works" label="预想故障类型" width="120" align="center"></el-table-column>
          <el-table-column prop="works" label="预想故障设备" width="120" align="center"></el-table-column>
          <el-table-column prop="works" label="风险等级(措施前)" width="150" align="center"></el-table-column>
          <el-table-column prop="works" label="损失负荷(Mw)(措施前)" width="180" align="center"></el-table-column>
          <el-table-column prop="works" label="风险等级(措施后)" width="150" align="center"></el-table-column>
          <el-table-column prop="works" label="损失负荷(Mw)(措施后)" width="180" align="center"></el-table-column>
        </el-table-column>
      </el-table>



       <!-- 弹窗--预警通知单 -->
    <Yujingtongzhidan v-if="local.Yujingtongzhidan_show"></Yujingtongzhidan> 
  </div>
</template>

<script>
import DataLocal from './DateLocal.js'
import Yujingtongzhidan from './Yujingtongzhidan'
// import { constants } from 'fs';
export default {
    data(){
       return {
         local:DataLocal,

         dormitory: [{
                people: '年风险评估-2020年',
                jindu:'未计算',
                meet: '年评估',
                place: 'xxx检修计划名称暗示法萨芬的撒方法萨芬萨芬撒分散',
                execg: '2012',
                works: '小张'
                }, {
                people: '年风险评估-2020年',
                jindu:'未计算',
                meet: '年评估',
                place: 'xxx检修计划名称',
                execg: '2019',
                works: '小夏'
                }],
                search: '',
          checked: false,
          currentRow: null,

　　　　　　radio: '',  //单选默认为空
          templateSelection: [],  //单选数据存放的数组

       }
    },
    created(){
       
    },
    mounted(){
        console.log(this.$route.query.id)  //获取路由跳转的参数
        console.log(111)
    },
    computed: {
            // 模糊搜索
            tables () {
                const search = this.search
                if (search) {
                // filter() 方法创建一个新的数组，新数组中的元素是通过检查指定数组中符合条件的所有元素。
                // 注意： filter() 不会对空数组进行检测。
                // 注意： filter() 不会改变原始数组。
                return this.dormitory.filter(data => {
                    // some() 方法用于检测数组中的元素是否满足指定条件;
                    // some() 方法会依次执行数组的每个元素：
                    // 如果有一个元素满足条件，则表达式返回true , 剩余的元素不会再执行检测;
                    // 如果没有满足条件的元素，则返回false。
                    // 注意： some() 不会对空数组进行检测。
                    // 注意： some() 不会改变原始数组。
                    return Object.keys(data).some(key => {
                    // indexOf() 返回某个指定的字符在某个字符串中首次出现的位置，如果没有找到就返回-1；
                    // 该方法对大小写敏感！所以之前需要toLowerCase()方法将所有查询到内容变为小写。
                    return String(data[key]).toLowerCase().indexOf(search) > -1
                    })
                })
                }
                return this.dormitory
            }
        },
   
    methods:{
      tableHeaderColor({ row, column, rowIndex, columnIndex }) {
              if (rowIndex === 0 || rowIndex === 1) {
                  return 'background:linear-gradient(0deg,rgba(230,230,230,0.8),rgba(255,255,255,0.8));color:rgba(77,77,77,1);;font-weight:400;font-size:12px;text-align:center'
              }
          },
      tableRowClassName({row, rowIndex}) {
              if(rowIndex%2==1){
                  return 'warning-row';
              }else{
                  return 'success-row';
              }
          },
      Shengchengyujingdan(){
         this.local.Yujingtongzhidan_show = true;
      },
      check(event){  //隐藏已生成的预警单事件
        console.log(event)
      },
      getCurrentRow(row){ //获取选中数据
        console.log(row)
        this.templateSelection = row;
      },
    },
    
    components: {
        Yujingtongzhidan
    },
    
}
</script>

<style>
#Fengxianpinggu_Detail th{
    padding: 0px;
}
.el-table .warning-row {
    background:rgba(231,247,255,1);
  }
.el-table .success-row {
  background:rgba(255,255,255,1);
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0 0;
}
body{margin: 0 0}
.Fengxianpinggu_Detail{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 16px;
  color: #fff;
  background: #339ED3
}
.Fengxianpinggu_Detail img{
  margin-left: 10px;
  margin-right: 5px;
  float: left;
  margin-top: 10px;
}
.Fengxianpinggu_Detail div{
  font-size:14px;
  font-family:SimHei;
  font-weight:bold;
  color:rgba(255,255,255,1);
}
.Fengxianpinggu_Detail_Button
{
  background: #F0F0F0;
  height:44px;
  border-bottom: 1px solid #D9D9D9;
  background:linear-gradient(0deg,rgba(255,255,255,0.8),rgba(217,217,217,0.8),rgba(242,242,242,0.8),rgba(255,255,255,0.8));
  box-shadow:0px 2px 6px 0px rgba(102,102,102,0.2);
}

.Fengxianpinggu_Detail_Button li{
  width:120px;
  height:44px;
  line-height: 44px;
  text-align: center;
  cursor: pointer;
  border-right:1px solid rgba(218,218,218,1);
  font-size:12px;
  font-family:Microsoft YaHei;
  font-weight:400;
  color:rgba(102,102,102,1);
  float: left;
}


.Fengxianpinggu_monthDetail_search{
  /* width:1366px; */
  height:35px;
  line-height: 34px;
  background:linear-gradient(0deg,rgba(215,238,252,0.8),rgba(255,255,255,0.8));
  /* border:2px solid rgba(204,220,229,1); */
}
.Fengxianpinggu_monthDetail_search img{
  position: absolute;
  left: 16px;
  top: 89px;
}
.Fengxianpinggu_monthDetail_search input{
  width:300px;
  height:22px;
  background:rgba(255,255,255,1);
  border:1px solid rgba(204,204,204,1);
  /* box-shadow:0px 3px 3px 0px rgba(153,153,153,0.6); */
  border-radius:2px;
  margin-left: 10px;
  padding-left: 23px;
}
.Yujingdan{
  height:20px;
  background:linear-gradient(0deg,rgba(1,100,163,0.8),rgba(45,160,233,0.8));
  box-shadow:0px 2px 3px 0px rgba(102,102,102,0.5);
  border-radius:2px;
  border: none;
  color: #fff;
  position: absolute;
  right: 15px;
  top: 85px;
  cursor: pointer;
}
.el-radio__label{
  display: none
}
.aaaa{overflow: hidden; word-break:break-all;-webkit-line-clamp:2;-webkit-box-orient:vertical; display: -webkit-box;}
.aaaa:hover{
    display: block;
    width: 100%;
    overflow: auto;
    white-space: normal;
}
</style>



