<template>
  <el-dialog id="MaintenancePlan"  :visible.sync="local.MaintenancePlan_show" width="1366px" :close-on-click-modal="false">
    <div slot="title" class="MaintenancePlan-title">
      <img src="../../static/img/风险评估标题.png">
      <div>风险评估-检修计划查看</div>
      </div>
   
    <el-table border :data="dormitory" style="width: 100%" max-height="520px" 
    :header-cell-style="tableHeaderColor" :row-class-name="tableRowClassName">
      <el-table-column type="index" width="50" label="序号"></el-table-column>
          <!-- <el-table-column type="selection"  label-class-name="全选" width="55" align="center"></el-table-column> -->
      <el-table-column prop="id" label="ID" width="100" align="center"></el-table-column>
      <el-table-column prop="jindu"  label="分组编号" width="100" align="center">
          <!-- <a href="">已生成预警单</a> -->
      </el-table-column>
      <el-table-column prop="meet"  label="上报地区" align="center"></el-table-column>
      <el-table-column prop="place" label="设备类型" width="100" align="center"></el-table-column>
      <el-table-column prop="execg" label="设备名称" width="120" align="center"></el-table-column>
      <el-table-column prop="works" label="RDFID" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="设备归属" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="停电用户" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="停电范围" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="开始时间" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="结束时间" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="持续天数" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="开始月份" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="注释1" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="注释2" width="100" align="center"></el-table-column>
      <el-table-column prop="works" label="注释3" width="100" align="center"></el-table-column>
    </el-table>
  </el-dialog>
</template>

<script>
import DataLocal from './DateLocal.js'
// import { constants } from 'fs';
export default {
    data(){
       return {
         local:DataLocal,

        //  dormitory: [{
        //         id: '123214',
        //         jindu:'未计算',
        //         meet: '年评估',
        //         place: 'xxx检修计划名称',
        //         execg: '2012',
        //         works: '小张'
        //         }, {
        //         id: '24124',
        //         jindu:'未计算',
        //         meet: '年评估',
        //         place: 'xxx检修计划名称',
        //         execg: '2019',
        //         works: '小夏'
        //         }],
                search: ''

       }
    },
    created(){
       
    },
    mounted(){
        
    },
   
    methods:{
      tableHeaderColor({ row, column, rowIndex, columnIndex }) {
              if (rowIndex === 0) {
                  return 'background:linear-gradient(0deg,rgba(230,230,230,0.8),rgba(255,255,255,0.8));color:rgba(77,77,77,1);;font-weight:400;font-size:12px;text-align:center'
              }
          },
          tableRowClassName({row, rowIndex}) {
              if(rowIndex%2==1){
                  return 'warning-row';
              }else{
                  return 'success-row';
              }
          },
    },
    
}
</script>

<style>
#MaintenancePlan .el-dialog__body {
    padding: 0px 0px;
}
#MaintenancePlan .el-dialog__header {
  padding: 0;
  height:30px;
  line-height: 30px;
  background:rgba(51,158,211,1);
  border-radius:5px 5px 0px 0px;

}
.MaintenancePlan-title img{
  margin-right: 5px;
  float: left;
  margin-top: 10px;
}
.MaintenancePlan-title div{
  font-size:14px;
  font-family:SimHei;
  font-weight:bold;
  color:rgba(255,255,255,1);
}

/* .el-dialog__footer{
  padding: 0 0 10px 0;
  text-align: left;
} */
#MaintenancePlan .MaintenancePlan-title{
  width: 280px;
  font-size:12px;
  font-family:Microsoft YaHei;
  font-weight:bold;
  color:rgba(255,255,255,1);
  margin-left: 10px
}
#MaintenancePlan .el-dialog__headerbtn{
  top: 8px;
}
.el-dialog__headerbtn .el-dialog__close {
    color: #fff;
}
.el-dialog__headerbtn:focus .el-dialog__close, .el-dialog__headerbtn:hover .el-dialog__close {
    color: #ffffff;
}
.el-table .warning-row {
    background:rgba(231,247,255,1);
  }
.el-table .success-row {
  background:rgba(255,255,255,1);
}
</style>



