// 公共的js
//获取月最大天数
export function _maxDate(str) {
  if (typeof str !== 'string' || str.indexOf('-') == -1) throw '方法错误: _maxDate()方法的参数必须是 [ 字符串 ], 格式必须为 [ 年-月 ] 或者 [ 年-月-... ]!'
  let _y = str.split('-')[0];
  let _m = str.split('-')[1];
  return new Date(parseInt(_y), parseInt(_m), 0).getDate();
}

export function getWeek(dt){
  // function getWeek(dt){
    let d1 = new Date(dt);
    let d2 = new Date(dt);
    d2.setMonth(0);
    d2.setDate(1);
    let rq = d1-d2;
    let days = Math.ceil(rq/(24*60*60*1000));
    let num = Math.ceil(days/7);
    return num;
// }
}

