<template>
  <el-dialog id="YishengchengYujingdan"  :visible.sync="local.YishengchengYujingdan" width="1366px" :close-on-click-modal="false">
    <div slot="title" class="dialog-title">已生成预警单</div>
    <div class="YishengchengYujingdan_search">
      <img src="../../static/img/search.png">
      <input type="text" v-model="search" placeholder="搜索">
    </div>
    <el-table border :data="tables" style="width: 100%" max-height="520px" 
    :header-cell-style="tableHeaderColor" :row-class-name="tableRowClassName">
    <el-table-column type="index" width="50" label="序号"></el-table-column>
        <!-- <el-table-column type="selection"  label-class-name="全选" width="55" align="center"></el-table-column> -->
    <el-table-column prop="people" label="标题" width="300" align="center"></el-table-column>
    <el-table-column prop="jindu"  label="编号" width="100" align="center">
        <!-- <a href="">已生成预警单</a> -->
    </el-table-column>
    <el-table-column prop="meet"  label="停电设备" align="center"></el-table-column>
    <el-table-column prop="place" label="预警开始时间" width="120" align="center"></el-table-column>
    <el-table-column prop="execg" label="预警结束时间" width="120" align="center"></el-table-column>
    <el-table-column prop="works" label="生成时间" width="160" align="center"></el-table-column>
    <el-table-column prop="works" label="生成人员" width="100" align="center"></el-table-column>
    <el-table-column label="操作"  align="center" width="200">
      <template slot-scope="scope">
        <button @click="YishengchengYujingdan_edit"><img src="../../static/img/修改.png"> 编辑</button>
        <button @click="YishengchengYujingdan_del(scope.$index,scope.row)"><img src="../../static/img/删除.png"> 删除</button>
      </template>
    </el-table-column>
    </el-table>

     <!-- 弹窗--预警通知单 -->
    <Yujingtongzhidan v-if="local.Yujingtongzhidan_show"></Yujingtongzhidan> 
  </el-dialog>
  
</template>

<script>
import DataLocal from './DateLocal.js'
import Yujingtongzhidan from './Yujingtongzhidan'
// import { constants } from 'fs';
export default {
    data(){
       return {
         local:DataLocal,

         dormitory: [{
                people: '年风险评估-2020年',
                jindu:'未计算',
                meet: '年评估',
                place: 'xxx检修计划名称',
                execg: '2012',
                works: '小张'
                }, {
                people: '年风险评估-2020年',
                jindu:'未计算',
                meet: '年评估',
                place: 'xxx检修计划名称',
                execg: '2019',
                works: '小夏'
                }],
                search: ''

       }
    },
    created(){
       
    },
    mounted(){
        
    },
    computed: {
            // 模糊搜索
            tables () {
                const search = this.search
                if (search) {
                // filter() 方法创建一个新的数组，新数组中的元素是通过检查指定数组中符合条件的所有元素。
                // 注意： filter() 不会对空数组进行检测。
                // 注意： filter() 不会改变原始数组。
                return this.dormitory.filter(data => {
                    // some() 方法用于检测数组中的元素是否满足指定条件;
                    // some() 方法会依次执行数组的每个元素：
                    // 如果有一个元素满足条件，则表达式返回true , 剩余的元素不会再执行检测;
                    // 如果没有满足条件的元素，则返回false。
                    // 注意： some() 不会对空数组进行检测。
                    // 注意： some() 不会改变原始数组。
                    return Object.keys(data).some(key => {
                    // indexOf() 返回某个指定的字符在某个字符串中首次出现的位置，如果没有找到就返回-1；
                    // 该方法对大小写敏感！所以之前需要toLowerCase()方法将所有查询到内容变为小写。
                    return String(data[key]).toLowerCase().indexOf(search) > -1
                    })
                })
                }
                return this.dormitory
            }
        },
   
    methods:{
      tableHeaderColor({ row, column, rowIndex, columnIndex }) {
              if (rowIndex === 0) {
                  return 'background:linear-gradient(0deg,rgba(230,230,230,0.8),rgba(255,255,255,0.8));color:rgba(77,77,77,1);;font-weight:400;font-size:12px;text-align:center'
              }
          },
          tableRowClassName({row, rowIndex}) {
              if(rowIndex%2==1){
                  return 'warning-row';
              }else{
                  return 'success-row';
              }
          },
          YishengchengYujingdan_del(index, rows){
            console.log(index)
            console.log(rows.people)
            this.$confirm("是否删除"+"["+rows.people+"]？", '删除', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              // this.fileList = []
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            })
            // rows.splice(index, 1);
          },
          YishengchengYujingdan_edit(){
            // alert(1)
            this.local.Yujingtongzhidan_show = true;
          }
    
   
    },
    components: {
        Yujingtongzhidan
    },
    
    
}
</script>

<style>
#YishengchengYujingdan .el-dialog__body {
    padding: 0px 0px;
}
.el-dialog__header {
  padding: 0;
  height:30px;
  line-height: 30px;
  background:rgba(51,158,211,1);
  border-radius:5px 5px 0px 0px;

}

.el-dialog__footer{
  padding: 0 0 10px 0;
  text-align: left;
}
.dialog-title{
  width: 80px;
  font-size:12px;
  font-family:Microsoft YaHei;
  font-weight:bold;
  color:rgba(255,255,255,1);
  margin-left: 10px
}
.el-dialog__headerbtn{
  top: 8px;
}
.el-dialog__headerbtn .el-dialog__close {
    color: #fff;
}
.el-dialog__headerbtn:focus .el-dialog__close, .el-dialog__headerbtn:hover .el-dialog__close {
    color: #ffffff;
}
.YishengchengYujingdan_search{
  width:1366px;
  height:35px;
  line-height: 34px;
  background:linear-gradient(0deg,rgba(215,238,252,0.8),rgba(255,255,255,0.8));
  /* border:2px solid rgba(204,220,229,1); */
}
.YishengchengYujingdan_search img{
  position: absolute;
  left: 13px;
  top: 41px;
}
.YishengchengYujingdan_search input{
  width:300px;
  height:22px;
  background:rgba(255,255,255,1);
  border:1px solid rgba(204,204,204,1);
  /* box-shadow:0px 3px 3px 0px rgba(153,153,153,0.6); */
  border-radius:2px;
  margin-left: 10px;
  padding-left: 23px;
}
.el-table .warning-row {
    background:rgba(231,247,255,1);
  }
.el-table .success-row {
  background:rgba(255,255,255,1);
}
</style>



