<template>
  <div @click="off">
    <div class="Fengxianpinggu_Tile">
      <img src="../../static/img/风险评估标题.png">
      <div>风险评估</div>
    </div>
    <ul class="Function_Button">
      <li :class="{beijing:local.fengxianpinggu.changeActive == 0}"  @click="CheckMode(0,0)">
        新增风险评估
      </li>
      <li :class="{beijing:local.fengxianpinggu.changeActive == 1}"  @click="CheckMode(1,1)" >
        已生成预警单
      </li>
      <li :class="{beijing:local.fengxianpinggu.changeActive == 2}"  @click.stop="CheckMode(2,2)">
        配置模块
        <img src="../../static/img/配置模块下拉.png" style="margin-bottom: -2px;margin-right: -10px;">

        <!-- 备自投 第二版本做 -->
        <!-- <div class="peizhi" v-show="local.fengxianpinggu.peizhi" @click="beizitoupeizhi"> 
          备自投配置
        </div> -->
      </li>
    </ul>
    <!-- 当前方式校核详情 -->
    <TableDetail v-show="local.isShowing"></TableDetail>

     <!-- 弹窗--备自投配置 -->
    <!-- <Beizhitoupeizhi v-if="local.beizitou_show"></Beizhitoupeizhi>  -->
  </div>
</template>

<script>
import TableDetail from './TableDetail.vue'
import DataLocal from './DateLocal.js'
// import Beizhitoupeizhi from './Beizhitoupeizhi'
export default {
  data () {
    return {
      local:DataLocal,
    }
  },
  components: {
    TableDetail,
    // Beizhitoupeizhi
  },
  methods: {
      off(){
        this.local.fengxianpinggu.peizhi = false
      },
      CheckMode(number,type){
        this.local.fengxianpinggu.changeActive = number
        if(number == 2){
          this.local.fengxianpinggu.peizhi = true
        }
        if(number == 0){
          this.local.FengxianpingguSee_show = true
        }
        if(number == 1){
          this.local.YishengchengYujingdan = true
        }

      },
      // beizitoupeizhi(){
      //   this.local.beizitou_show = true
      // }
  },
}
</script>

<style scoped>
* {
    box-sizing: border-box;
}
a{
  color: #000
}
span{
  font-size: 12px
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0 0;
}
.actives {
  background:linear-gradient(0deg,rgba(63,184,10,1),rgba(82,235,66,1)) !important;
}
body{margin: 0 0}
.Fengxianpinggu_Tile{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 16px;
  color: #fff;
  background: #339ED3
}
.Fengxianpinggu_Tile img{
  margin-left: 10px;
  margin-right: 5px;
  float: left;
  margin-top: 10px;
}
.Fengxianpinggu_Tile div{
  font-size:14px;
  font-family:SimHei;
  font-weight:bold;
  color:rgba(255,255,255,1);
}
.Function_Button
{
  background: #F0F0F0;
  height:44px;
  border-bottom: 1px solid #D9D9D9;
  background:linear-gradient(0deg,rgba(255,255,255,0.8),rgba(217,217,217,0.8),rgba(242,242,242,0.8),rgba(255,255,255,0.8));
  box-shadow:0px 2px 6px 0px rgba(102,102,102,0.2);
}

.Function_Button li{
  width:120px;
  height:44px;
  line-height: 44px;
  text-align: center;
  cursor: pointer;
  border-right:1px solid rgba(218,218,218,1);
  font-size:12px;
  font-family:Microsoft YaHei;
  font-weight:400;
  color:rgba(102,102,102,1);
  float: left;
}
.beijing{
  background:rgba(245,248,250,1);
  box-shadow:0px 1px 3px 0px rgba(0, 0, 0, 0.2);
}
.peizhi{
    position: absolute;
    width: 120px;
    z-index: 99;
    background:rgba(250,253,255,1);
    border:1px solid rgba(230,230,230,1);
    box-shadow:0px 2px 6px 0px rgba(102,102,102,0.2);
    border-radius:0px 0px 5px 5px;
    font-size:12px;
    font-family:Microsoft YaHei;
    font-weight:400;
    color:rgba(102,102,102,1);
}
</style>



